/* eslint-disable */
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import * as s from "../style/LoginPageStyle";
import { initializeApp } from "firebase/app";
import {
  getAuth,
  signInWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
} from "firebase/auth";

const firebaseConfig = {
  apiKey: "AIzaSyBuNouLpDM-yluSDCzzYn-XKJZgQglMpGA",
  authDomain: "fanfantv-c7537.firebaseapp.com",
  projectId: "fanfantv-c7537",
  storageBucket: "fanfantv-c7537.appspot.com",
  messagingSenderId: "3889383774",
  appId: "1:3889383774:web:7e9c4e28821eff351067cf",
  measurementId: "G-Y2NF0C4DQE",
};

const app = initializeApp(firebaseConfig);
const auth = getAuth();

const LoginPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [redirect, setRedirect] = useState(false);
  const [errorText, setErrorText] = useState("");

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        console.log("User is signed in");
        setRedirect(true);
      } else {
        console.log("User is signed out");
      }
    });

    return () => unsubscribe();
  }, [auth]);

  useEffect(() => {
    if (redirect) {
      setRedirect(false);
      window.location.href = "/";
    }
  }, [redirect]);

  const handleLogin = async () => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
    } catch (error) {
      console.error("Error logging in:", error.message);
      setErrorText(getErrorMessage(error.code));
    }
  };

  const getErrorMessage = (errorCode) => {
    switch (errorCode) {
      case "auth/invalid-email":
        return "   .";
      case "auth/user-not-found":
        return "   .";
      case "auth/wrong-password":
        return "  .";
      case "auth/too-many-requests":
        return "    .    .";
      case "auth/missing-password":
        return " .";
      case "auth/network-request-failed":
        return "  .    .";
      default:
        return "   .    .";
    }
  };

  return (
    <s.LoginContainer>
      <s.LoginBox>
        <Link to="/">
          <s.FANFANTVTitle>FANFANTV</s.FANFANTVTitle>
        </Link>
        <s.EmailInputBox
          type="email"
          placeholder=" "
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        ></s.EmailInputBox>
        <s.PasswordInputBox
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        ></s.PasswordInputBox>
        <s.ErrorText>{errorText}</s.ErrorText>
        <s.ReminderText>
                
          .
        </s.ReminderText>
        <s.LoginButton onClick={handleLogin}></s.LoginButton>
        <Link to="/forgot-password">
          <s.ForgotPasswordText> ?</s.ForgotPasswordText>
        </Link>
      </s.LoginBox>
      <s.SignUpBox>
        <Link to="/signup">
          <s.SignUpText>  ?</s.SignUpText>
        </Link>
      </s.SignUpBox>
    </s.LoginContainer>
  );
};

export default LoginPage;
